<template>
    <div class="flex">
       <img src="/static/img/avatar2.png" class="avatar"/>
       <span class="title bb">日志</span>
       <div style="margin-bottom:35px">
           <div class="year" v-for="year in noteList" :key="year[0].date.substring(0,4)">
               <div class="year-title">{{year[0].date.substring(0,4)}}</div>
               <ul class="content">
                   <li v-for="item in year" :key="item.fileId">
                       <div class="at-title">
                           <router-link :to="{ path:'/show',query:{ type:this.$comm.type_note,fileId:item.fileId } }">{{item.title}}</router-link>
                        </div>
                       <div class="at-date">{{item.date.substring(5)}}</div>
                   </li>
               </ul>
           </div>
       </div>
    </div>
    <StatusLayout ref="sl"/>
    <img src="/static/img/arrow-up.svg" class="btn-up" @click="this.$toTop" v-if="noteList.length != 0">
</template>

<script>
import StatusLayout from '../components/StatusLayout.vue'
export default {
    name: 'Note',
    components:{
        StatusLayout
    },
    computed:{
        noteList(){
            return this.$store.state.note.noteList
        }
    },
    methods:{

        // 获取JSON数据
        getData(){
            this.$refs.sl.loading = true
            this.$http.get('/static/data/note.json?q='+new Date().getTime())
                      .then(res => {
                          let arr = this.groupBy(res.data.data, function (item) {
                                        return [item.date.substring(0,4)];
                                    })
                            
                          if(arr.length == 0){
                            this.$refs.sl.empty = true
                          }
                          else{
                            this.$store.commit("setNoteList",arr)
                            this.$refs.sl.loading = false
                          }
                      })
                      .catch(error => {
                          this.$refs.sl.error = true
                          console.log(error)
                      })
        },

        // 分组
        groupBy(array, f){
            const groups = {}      
            array.forEach(function (o) {
                const group = JSON.stringify(f(o))
                groups[group] = groups[group] || []
                groups[group].push(o)
            });
            return Object.keys(groups).map(function (group) {
                return groups[group]
            }); 
        },
    },
    mounted(){
        if(this.noteList.length == 0){
            this.getData()
        }
    }
}

// https://octodex.github.com/ <= 头像
</script>

<style scoped>
    .avatar{
        width: 30%;
        margin: 0 auto;
        margin-top: 15px;
        margin-bottom: 15px;
    }
    .title{
        font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
        font-size: 2em;
        display: inline-block;
        text-align: center;
        margin: 5px 0 25px 0; 
        padding-bottom: 15px;
    }

    .year{
        margin-bottom: 35px;
    }

    .year-title{
        font-family: "Menlo", "Meslo LG", monospace;
        font-size: 32px;
        font-weight: bold;
        margin: 0 0 10px 0;
    }

    .year-title::before{
        content: '#';
        color: #6d9ee7;
        display: inline-block;
        padding-right: 8px;
    }

    .content{
        list-style: none;
    }

    .content>li{
        display: flex;
        flex-direction: row;
        margin: 15px 0 15px 0;
        padding: 5px 0 5px 0;
    }

    .at-title{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        flex: 1;
    }
    .at-title>a{
        color: black;
        text-decoration: none;
    }

    .at-title>a:hover{
        text-decoration: underline;
    }

    .at-date{
        text-align: right;
    }
</style>